"use client";

import React, { useState, useEffect } from "react";
import { FaUniversity, FaCheckCircle } from "react-icons/fa";
import Image from "next/image";
import Link from "next/link";
import DonationCard from "./DonationCard";
import useCampaignStore from "@/store/campaignStore";
import { CampaignCardSkeleton } from "./LoadingSkeleton";

export default function FundRaised() {
    const [completedCampaigns, setCompletedCampaigns] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const { getCampaigns } = useCampaignStore();

    useEffect(() => {
        const fetchCompletedCampaigns = async () => {
            try {
                setIsLoading(true);
                // Fetch campaigns without status filter (backend will return active+completed for non-admin)
                // Then filter on frontend for completed campaigns
                const response = await getCampaigns({
                    limit: 100, // Get more to filter completed ones
                    sortBy: '-createdAt'
                });
                
                const allCampaigns = response.data || response.campaigns || [];
                
                // Filter for completed campaigns
                // A campaign is completed if:
                // 1. status is 'completed', OR
                // 2. currentAmount >= goalAmount, OR
                // 3. daysRemaining <= 0
                const completed = allCampaigns.filter(campaign => {
                    return campaign.status === 'completed' || 
                           (campaign.currentAmount >= campaign.goalAmount) ||
                           (campaign.daysRemaining !== undefined && campaign.daysRemaining <= 0);
                }).slice(0, 20); // Limit to 20 most recent
                
                setCompletedCampaigns(completed);
            } catch (error) {
                console.error("Failed to fetch completed campaigns:", error);
                console.error("Error details:", {
                    message: error?.message,
                    response: error?.response?.data,
                    status: error?.response?.status
                });
                // Don't show error to user, just show empty state
                setCompletedCampaigns([]);
            } finally {
                setIsLoading(false);
            }
        };

        fetchCompletedCampaigns();
    }, [getCampaigns]);

    return (
        <section className="container mx-auto py-16 bg-gradient-to-r from-green-50 to-pink-50 px-6">
            
            <div className="mb-10 text-center relative bg-white rounded-2xl border border-green-200 shadow-[0_4px_20px_rgba(72,187,120,0.3)] p-20 hover:shadow-[0_8px_30px_rgba(72,187,120,0.5)] transition-all duration-500">
                <h5 className="text-green-600 font-semibold uppercase tracking-widest animate-pulse">
                    Completed Campaigns
                </h5>
                <h1 className="text-3xl md:text-4xl font-extrabold text-gray-600">
                    Fundraising is at the heart of what we do
                </h1>

                <div className="w-24 md:w-40 h-1 bg-gradient-to-r from-green-500 via-lime-400 to-pink-500 mx-auto mt-4 rounded-full shadow-[0_0_15px_rgba(72,187,120,0.6)]"></div>

                <p className="mt-4 text-gray-600 text-lg">
                    Celebrating successful campaigns that made a difference
                </p>
            </div>

            {/* Completed Campaigns Grid */}
            {isLoading ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-12">
                    {[...Array(6)].map((_, i) => (
                        <CampaignCardSkeleton key={i} />
                    ))}
                </div>
            ) : completedCampaigns.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-12">
                    {completedCampaigns.map((campaign) => {
                        const progressPercentage = Math.round((campaign.currentAmount / campaign.goalAmount) * 100);
                        
                        return (
                            <Link key={campaign._id} href={`/campaigns/${campaign._id}`}>
                                <div className="bg-white rounded-xl shadow-md hover:shadow-2xl transition-all duration-300 overflow-hidden cursor-pointer border-2 border-green-200 transform hover:-translate-y-1">
                                    {/* Campaign Image */}
                                    <div className="relative h-48 bg-gray-200 overflow-hidden">
                                        {campaign.images && campaign.images[0] ? (
                                            <img
                                                src={campaign.images[0].url}
                                                alt={campaign.title}
                                                className="w-full h-full object-cover hover:scale-110 transition-transform duration-300"
                                            />
                                        ) : (
                                            <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-green-100 to-green-200">
                                                <span className="text-gray-400 text-4xl">ðŸ“·</span>
                                            </div>
                                        )}
                                        <div className="absolute top-3 right-3 bg-green-500 text-white px-3 py-1 rounded-full text-xs font-bold shadow-lg flex items-center gap-1">
                                            <FaCheckCircle /> Completed
                                        </div>
                                    </div>

                                    {/* Campaign Info */}
                                    <div className="p-4">
                                        <span className="inline-block bg-green-100 text-green-800 text-xs px-3 py-1 rounded-full mb-2">
                                            {campaign.category}
                                        </span>

                                        <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">
                                            {campaign.title}
                                        </h3>

                                        <p className="text-gray-600 text-sm mb-4 line-clamp-2">
                                            {campaign.shortDescription}
                                        </p>

                                        {/* Progress Bar - Full */}
                                        <div className="mb-3">
                                            <div className="flex justify-between text-sm mb-1">
                                                <span className="text-gray-600">Raised</span>
                                                <span className="text-green-600 font-bold">
                                                    100%
                                                </span>
                                            </div>
                                            <div className="w-full bg-gray-200 rounded-full h-2">
                                                <div
                                                    className="bg-green-500 h-2 rounded-full"
                                                    style={{ width: '100%' }}
                                                ></div>
                                            </div>
                                        </div>

                                        {/* Stats */}
                                        <div className="flex justify-between items-center text-sm">
                                            <div>
                                                <span className="text-gray-900 font-bold">
                                                    â‚¹{campaign.currentAmount?.toLocaleString() || '0'}
                                                </span>
                                                <span className="text-gray-500"> raised</span>
                                            </div>
                                            <div className="text-gray-600">
                                                Goal: â‚¹{campaign.goalAmount?.toLocaleString() || '0'}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        );
                    })}
                </div>
            ) : (
                <div className="text-center py-12 mb-12">
                    <div className="text-6xl mb-4">ðŸŽ‰</div>
                    <h3 className="text-2xl font-bold text-gray-800 mb-2">No Completed Campaigns Yet</h3>
                    <p className="text-gray-600">Completed campaigns will appear here once they reach their goals!</p>
                </div>
            )}

           <DonationCard />
        </section>
    );
}


